import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { BehaviorSubject } from 'rxjs';
import { AuthService } from '../../shared/services/auth.service';

export interface NavigationLink {
  protected: boolean;
  active: boolean;
  routerLink: string;
  label: string;
}
@Component({
  selector: 'lehi41-navigation',
  styles: [
    '.logo { height: 50px; width: 50px;}',
    '.nav-item { cursor: pointer; }',
  ],
  template: `
    <nav class="navbar navbar-expand-lg navbar-light mt-3">
      <div class="container l41-container">
        <a
          class="navbar-brand d-flex align-items-center"
          (click)="setDocTitle('Lehi Fataha | Home')"
          routerLink="/home"
        >
          <img src="./assets/logo.png" class="logo" />
          <span class="ml-2">Lehi Fataha</span>
        </a>

        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbar-items"
          aria-controls="navbar-items"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbar-items">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item" *ngFor="let link of links$ | async">
              <a class="nav-item nav-link" routerLink="{{ link.routerLink }}">{{
                link.label
              }}</a>
            </li>
            <li class="nav-item">
              <a
                class="nav-item nav-link"
                (click)="setDocTitle('Lehi Fataha | Home')"
                routerLink="/login"
                *ngIf="!authenticated"
                >Login</a
              >
              <a
                class="nav-item nav-link"
                *ngIf="authenticated"
                (click)="logout()"
                >Logout</a
              >
            </li>
          </ul>
        </div>
      </div>
    </nav>
  `,
})
export class NavigationComponent implements OnInit {
  linksList: NavigationLink[] = [
    {
      protected: true,
      active: false,
      routerLink: '/meet',
      label: 'Meet',
    },
    {
      protected: true,
      active: true,
      routerLink: '/cheat-sheet',
      label: 'Cheat Sheet',
    },
    {
      protected: true,
      active: false,
      routerLink: '/zoom-meeting',
      label: 'Zoom Meeting',
    },
  ];

  links$: BehaviorSubject<NavigationLink[]> = new BehaviorSubject<
    NavigationLink[]
  >(this.linksList.filter((l) => l.active && !l.protected));
  authenticated: boolean;

  constructor(
    private authService: AuthService,
    private router: Router,
    private titleService: Title
  ) {}

  ngOnInit(): void {
    this.authenticated = this.authService.isLoggedIn();
    if (this.authenticated) {
      const links = this.linksList.filter((l) => l.active);
      this.links$.next(links);
    }
  }

  logout(): void {
    this.authService.logout();
    this.router.navigateByUrl('/home');
    this.authenticated = false;
    this.links$.next(this.linksList.filter((l) => l.active && !l.protected));
  }

  setDocTitle(title: string): void {
    this.titleService.setTitle(title);
  }
}
