import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Person } from '../..';
import { map, tap } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class PersonService {
  constructor(private firestore: AngularFirestore) {}

  get(): Observable<Person[]> {
    return this.firestore
      .collection('people')
      .snapshotChanges()
      .pipe(
        map((d) => {
          const people: Person[] = [];
          d.forEach((dd) => {
            people.push({
              id: dd.payload.doc.id,
              // @ts-ignore
              name: dd.payload.doc.data().name,
            } as Person);
          });
          return people;
        })
      );
  }

  create(person: Person) {
    return this.firestore.collection('people').add(person);
  }

  update(person: Person) {
    delete person.id;
    this.firestore.doc('people/' + person.id).update(person);
  }

  delete(id: string) {
    this.firestore.doc('people/' + id).delete();
  }
}
