import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './pages/home-page/home-page.component';
import { PageNotFoundPageComponent } from './pages/page-not-found-page/page-not-found-page.component';
import { LoginPageComponent } from './pages/login-page/login-page.component';
import { AuthGuard } from './shared/auth.guard';
import { MainLayoutComponent } from './layouts/main-layout/main-layout.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
  {
    path: 'login',
    component: LoginPageComponent,
  },
  {
    path: '',
    component: MainLayoutComponent,
    children: [
      {
        path: 'home',
        component: HomePageComponent,
      },
      {
        path: 'meet',
        canActivate: [AuthGuard],
        loadChildren: () => import('./features/meet/meet.module').then((m) => m.MeetModule),
      },
      {
        path: 'cheat-sheet',
        canActivate: [AuthGuard],
        loadChildren: () =>
          import('./features/cheat-sheet/cheat-sheet.module').then(
            (m) => m.CheatSheetModule
          ),
      },
      {
        path: 'zoom-meeting',
        canActivate: [AuthGuard],
        loadChildren: () =>
          import('./features/zoom-meeting/zoom-meeting.module').then(
            (m) => m.ZoomMeetingModule
          ),
      },
    ],
  },
  {
    path: '**',
    component: PageNotFoundPageComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
